/*SVG*/
.hanziPaint {
  width: 5vw;
  height: 5vw;
  transition: all 1s cubic-bezier(0.68, -0.55, 0.27, 1.55);
  border: 1px solid #ccc;
  border-radius: 3%;
}

.hanziPaint:hover {
  transform: scale(3);
  transform-origin: center center;
  background-color: #fff;
  z-index: 9999 !important;
  border: 3px solid #ccc;
  border-radius: 3%;
  position: relative;
}
